import React, {useState} from 'react'


export default function(props) {
    const handleUpClick = ()=>{
       // console.log("Uppercase Was Clicked" + text);
        let newText = text.toUpperCase();
        setText(newText)
        props.showAlert("Converted to UpperCase", "Success")
    }

    const handleLoClick = ()=>{
        // console.log("Uppercase Was Clicked" + text);
         let newText = text.toLowerCase();
         setText(newText)
        props.showAlert("Converted to LowerCase", "Success")

     }

     const handleClearClick = ()=>{
        // console.log("Uppercase Was Clicked" + text);
         let newText = '';
         setText(newText)
        props.showAlert("Text cleared", "Success")

     }

    const handleOnChange = (event)=>{
     //   console.log("On Change");
        setText(event.target.value);
    }
const handleCopy = ()=>{
    // console.log("I am Copy");
    var text = document.getElementById("myBox");
    text.select();
    // text.setSelectionRange(0, 9999);
    navigator.clipboard.writeText(text.value);
    props.showAlert("Copied to Clipboard", "Success")

}


const handleExtraSpaces =() =>{
    let newText = text.split(/[ ]+/);
    setText(newText.join(" "))
    props.showAlert("Extra Spaces Removed", "Success")

}

    const [text, setText] = useState('');
    return (
        <>
    <div className='container' style={{color:props.mode==='dark'?'white':'#181829c7'}}>
        <h1>{props.heading}</h1>
           <div className="mb-3">
           <textarea className="form-control" value={text} onChange={handleOnChange} style={{backgroundColor:props.mode==='dark'?'grey':'white', color:props.mode==='dark'?'white':'#181829c7'}} id="myBox" rows="8"></textarea>
           </div>
           <button className="btn btn-primary mx-2" onClick={handleUpClick}>Convert to Uppercase</button>
           <button className="btn btn-primary mx-2" onClick={handleLoClick}>Convert to Lowercase</button>
           <button className="btn btn-primary mx-2" onClick={handleClearClick}>Clear Text</button>
           <button className="btn btn-primary mx-2" onClick={handleCopy}>Copy Text</button>
           <button className="btn btn-primary mx-2" onClick={handleExtraSpaces}>Remove Extra Spaces</button>
           {/* <button className="btn btn-primary mx-2" onClick={handlePaste}>Paste</button> */}



    </div>
    <div className='container my-3' style={{color:props.mode==='dark'?'white':'#181829c7'}}>
        <h1>Your Text Summary</h1>
        <p>{text.split(" ").length} words and {text.length}characters</p>
        <p>{0.008 * text.split(" ").length} Minutes read</p>
        <h2>Preview</h2>
        <p>{text.length>0?text:"Enter something in the textbox to preview it here"}</p>

    </div>
    </>
  )
}
